.container-footer {
  padding-top: 80px;
  padding: 45px 25px;
  background: #0F172A;
}
.text-one > h1 {
  color: #fff;
  margin: 15px 0;
  font-size: 28px;
  text-transform: capitalize;
}
.text-one > h3 {
  color: var(--color-light);
  font-size: 20px;
  text-transform: capitalize;
}
.text-one > p {
  color: #fff;
  margin: 10px 0;
  font-size: 16px;
  line-height: 25px;
}
.text-two h3 {
  color: #fff;
  margin: 40px 0 0;
  font-size: 20px;
  text-transform: capitalize;
}
.text-two > ul {
  margin: 15px 0;
}
.text-two > ul li {
  color: #fff;
  font-size: 16px;
  line-height: 25px;
}
.container-footer > span {
  display: block;
  width: 100%;
  height: 1px;
  margin: 45px 0 25px;
  background: var(--color-light);
}
.container-footer > ul {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
}
.container-footer > ul li {
  width: 39px;
  height: 39px;
  line-height: 26px; 
  text-align: center;
  border-radius: 50%;
  border: .8px solid #E1E8F0;
  transition: all .3s ease;
}
.container-footer > ul li:hover {
  color: #fff;
  border: .8px solid transparent;
  background: var(--color-default); 
}
.container-footer > ul li a:hover {
  color: #fff;
}
.container-footer > ul li a {
  display: block;
  color: #E1E8F0; 
}
.container-footer > p {
  color: var(--color-light);
  margin: 25px 0;
  font-size: 16px;
  text-align: center;
}
.container-footer > p span:nth-child(1) {
  color: red;
}
.container-footer > p span:nth-child(2) {
  color: var(--color-default);
  font-weight: bold;
  text-transform: capitalize;
}
.container-footer > p span:nth-child(3) {
  color: rgb(0, 134, 255);
  font-weight: bold;
}

@media screen and (min-width: 700px) {
  .main.footer {
    display: flex;
    gap: 10%;
    align-items: flex-start;
    justify-content: center;
  }
}
