/*
flash message
*/

.main.contacts > p:nth-child(4) {
  color: var(--color-dark);
  padding: 15px 0;
  margin: 20px 30px;
  font-size: 15px;
  border-radius: 5px;
}
.los {
  border-left: 8px solid rgb(255, 0, 0);
  background-color: rgb(255, 0, 0, .2);
}
.succ {
  border-left: 8px solid rgb(20, 184, 166);
  background-color: rgb(20, 184, 166, .2);
}
.main.contacts.dark p:nth-child(4) {
  color: #fff;
}
/*
==================
    dark toggle
==================
*/
.dark-toggle {
  display: flex;
  align-items: center;
}
.dark-toggle > label {
  margin: 0 5px;
}
.dark-toggle > label > div {
  position: relative;
  width: 42px;
  height: 19px;
  border-radius: 20px;
  border: .1px solid var(--color-light);
  background-color: #fff;
}
.dark-toggle > label > div > div {
  position: absolute;
  top: 1.5px;
  left: 2px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: var(--background-default);
  transition: all .3s ease;
}

#dark-toggle:checked ~ label div.toggle-circle {
  transform: translateX(22px);
  background: #1E293B;
}
#dark-toggle-pc:checked ~ label div.toggle-circle {
  transform: translateX(22px);
  background: #1E293B;
}

.dark {
  color: #fff;
  background: var(--color-dark);
}

/* =================== */ 

.menu-bars.no-active {
  transform: translateX(500px);
}

.menu-bars {
  z-index: 999;
  position: fixed;
  top: 70px;
  right: 10px;
  width: 200px;
  padding: 15px;
  backdrop-filter: blur(20px);
  border-radius: 10px;
  background: rgb(255, 255, 255, .1);
  border: .5px solid var(--color-default);
  transform: translateX(0);
  transition: all .5s ease;
} 
.menu-bars.dark {
  background: rgb(15, 23, 24, .1);
}
.menu-bars ul li {
  color: var(--color-dark);
  padding: 5px 15px;
  font-size: 15px;
  cursor: pointer;
  border-radius: 5px;
  transition: all .3s ease;
}
.menu-bars.dark ul li {
  color: #fff;
}
.menu-bars ul li:hover {
  color: #fff;
  background: linear-gradient(90deg, rgba(20,184,166,1) 0%, rgba(20,184,166,0.5168035947359525) 100%);
}
.container-buttonTop {
  z-index: 999;
  position: fixed;
  right: 20px;
  bottom: 30px;
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 50%;
  background: var(--color-default);
  animation: 1s buttonAnimasi infinite;
}
.buttonTop {
  color: #fff;
  font-size: 50px;
}
.button-no-active {
  opacity: 0;
}
@keyframes buttonAnimasi {
  to {
    transform: scale(1);
  }
  from {
    transform: scale(1.1);
  }
}
/* =============================================== */
.container-hero {
  margin: 35px 20px;
}
.hero.top > h3 {
  color: var(--color-default);
  font-size: 16px;
}
.hero.top > h1 {
  color: var(--color-dark);
  font-size: 28px;
  margin: 6px 0 0;
  text-transform: capitalize;
}
.hero.top.dark > h1 {
  color: #fff;
}
.hero.top > p:nth-child(3) {
  color: var(--color-light);
  font-size: 16px;
  text-transform: capitalize;
}
.hero.top > p:nth-child(3) span {
  color: var(--color-dark);
}
.hero.top.dark > p:nth-child(3) span {
  color: #fff;
}
.hero.top > p:nth-child(4) {
  color: var(--color-light);
  margin: 20px 0;
  font-size: 16px;
  text-transform: capitalize;
}
.hero.top > p:nth-child(4) span {
  color: var(--color-dark);
  font-weight: 600;
}
.hero.top > button {
  border: none;
  margin: 10px 0;
  padding: 10px 0;
  transition: all .3s ease;
  border-radius: 20px;
  background: var(--background-default); 
}
.hero.top > button a {
  color: #fff;
  padding: 10px 15px;
  font-size: 16px;
  font-weight: bold;
}
.hero.top > button:hover {
  background: rgb(20, 184, 166, .8); 
}

/*
hero bottom 
*/

.hero.bottom,
.main.avatar {
  margin: 85px 0;
}
.hero.bottom > figure,
.main.avatar > figure {
  position: relative;
}
.hero.bottom > figure img,
.main.avatar > figure img {
  display: block;
  width: 300px;
  margin: 0 auto;
}
.hero.bottom > figure figcaption,
.main.avatar > figure figcaption {
  z-index: -1;
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.hero.bottom > figure > span {
  display: block;
  position: absolute;
  top: -70px;
  right: 0;
  color: #fff;
  width: 130px;
  height: 60px;
  padding: 10px 15px;
  border-radius: 20px 15px 20px 0;
  background: var(--color-default);
}
.hero.bottom > figure > span::before {
  position: absolute;
  left: -8px;
  bottom: -5px;
  content: '';
	border-bottom:solid 15px var(--background-default);
	border-left:solid 10px transparent;
	border-right:solid 10px transparent;
  transform: rotate(-135deg);
}
/*
container about 
*/

.container-about {
  margin: 0 25px;
}
.main.about > h3 {
  color: var(--color-default);
  font-size: 17px;
  text-transform: uppercase;
}
.main.about > p {
  color: var(--color-light);
  font-size: 16px;
  line-height: 25px;
  margin: 15px 0;
}
.main.about > p span:nth-child(1) {
  color: var(--color-default);
  text-transform: capitalize;
}
.main.about > p > a {
  color: var(--color-dark);
}
.main.about.dark > p > a {
  color: #fff;
}
.main.about > p span:nth-child(3) {
  color: var(--color-dark);
}
.main.about.dark > p span:nth-child(3) {
  color: #fff;
}
.main.about > h2 {
  color: var(--color-dark);
  font-size: 23px;
  margin-top: 30px;
}
.main.about.dark > h2 {
  color: #fff;
}
.main.about > ul {
  display: flex;
  margin: 20px 0;
  max-width: 180px;
  align-items: center;
  justify-content: space-between;
}
.main.about > ul li {
  width: 39px;
  height: 39px;
  line-height: 26px; 
  text-align: center;
  border-radius: 50%;
  border: .8px solid #E1E8F0;
  transition: all .3s ease;
}
.main.about > ul li:hover {
  color: #fff;
  border: .8px solid transparent;
  background: var(--color-default); 
}
.main.about > ul li a:hover {
  color: #fff;
}
.main.about > ul li a {
  display: block;
  color: #E1E8F0; 
}
.icons-medsos {
  margin-top: 6px;
  font-size: 25px;
}

/*
main avatar 
*/

.main.avatar {
  display: none;
  margin: 20px 0;
}

/*
main skils
*/

.main.skils {
  margin: 50px 25px 100px;
}
.main.skils > h2 {
  color: var(--color-default);
  font-size: 18px;
  text-transform: uppercase;
}
.main.skils > ul {
  margin: 15px 0;
}
.main.skils > ul li {
  font-size: 15px;
  margin: 15px 0 0;
  text-transform: uppercase;
}
.main.skils > ul li > span {
  display: block;
  width: 100%;
  height: 35px;
  margin-top: 5px;
  border-radius: 5px;
  background: #E1E8F0;
}
.main.skils > ul li > span::before {
  display: block;
  content: '';
  height: 35px;
  border-radius: 5px;
  background: var(--color-default);
} 
#html::before,
#css::before {
  width: 100%;
}
#js::before {
  width: 85%;
}
#php::before {
  width: 75%;
}
#python::before {
  width: 65%;
}

/*
main portfolio
*/
.main.portfolio {
  padding: 60px 25px;
  background: #1E293B;
}
.main.portfolio > h3 {
  color: var(--color-default);
  font-size: 18px;
  text-transform: capitalize;
  text-align: center;
}
.main.portfolio > h1 {
  color: #fff;
  margin: 10px 0;
  font-size: 27px;
  text-align: center;
  text-transform: capitalize;
}
.main.portfolio > p {
  color: var(--color-light);
  font-size: 16px;
  text-align: center;
}
.main.projeck {
  margin: 50px 0;
}
.main.projeck figure {
  width: 250px;
  margin: 50px auto;
}
.main.projeck figure img {
  display: block;
  width: 250px;
  margin: 0 auto;
  object-fit: cover;
  border-radius: 10px;
}
.main.projeck figure figcaption h3 {
  color: #fff;
  margin: 20px 0 10px;
  font-size: 18px;
}
.main.projeck figure figcaption p {
  color: var(--color-light);
}
/*
main contacts
*/
.main.contacts {
  padding-top: 70px;
  padding-bottom: 35px;
}
.main.contacts.dark {
  background: #1E293B;
}
.main.contacts > h4 {
  color: var(--color-default);
  font-size: 16px;
  text-align: center;
  text-transform: capitalize;
}
.main.contacts > h1 {
  color: var(--color-dark);
  margin: 13px 0;
  font-size: 28px;
  text-align: center;
  text-transform: capitalize;
}
.main.contacts.dark > h1 {
  color: #fff;
}
.main.contacts > p {
  color: var(--color-light);
  font-size: 16px;
  text-align: center;
}
.main.contacts > form {
  margin: 60px 25px;
}
.main.contacts > form label,
.main.contacts > form input,
.main.contacts > form button {
  display: block;
}
.main.contacts > form label {
  color: var(--color-default);
  margin: 5px 0;
  margin-top: 15px;
  font-size: 16px;
}
.main.contacts > form input {
  width: 100%;
  height: 45px;
  border: none;
  outline: none;
  padding: 0 15px;
  font-size: 16px;
  border-radius: 5px;
  background: #E1E8F0;
}
.main.contacts > form input:hover,
.main.contacts > form textarea:hover {
  border: 1px solid var(--color-default);
}
.main.contacts > form textarea {
  width: 100%;
  height: 150px;
  border: none;
  outline: none;
  padding: 15px 15px;
  font-size: 16px;
  border-radius: 5px;
  background: #E1E8F0;
}
.main.contacts > form button {
  color: #fff;
  width: 100%;
  height: 48px;
  border: none;
  margin: 20px 0;
  font-size: 17px;
  font-weight: bold;
  text-transform: capitalize;
  border-radius: 20px;
  background: var(--color-default);
}
@media screen and (min-width: 700px) {
  .container.header {
    padding: 15px 30px;
  }
  .icon-menu {
    display: none;
  }
  .main.header > ul {
    display: flex;
    gap: 20px;
    align-items: center;
    justify-content: space-between;
  }
  .main.header > ul li {
    color: var(--color-dark);
    font-size: 15px;
  }
  .main.header.dark > ul li {
    color: #fff;
  }
  .container-hero {
    display: flex;
    margin: 25px 30px;
    align-items: flex-start;
  }
  .hero.top,
  .hero.bottom {
    width: 50%;
  }
  .hero.bottom {
    margin: 56px 0;
  }
  .container-about {
    display: flex;
    gap: 10%;
    align-items: flex-start;
  }
  .main.avatar {
    display: block;
  }
  .main.about {
    width: 50%;
  }
  .container-skils {
    display: flex;
    gap: 10%;
    margin-top: 50px;
    align-items: center;
  }
  .main.skils {
    width: 50%;
  }
  .main.skils ul li span {
    height: 25px;
  }
  .main.skils ul li span::before {
    height: 25px;
  }
  .main.projeck {
    display: flex;
    align-items: flex-end;
  }
  .main.portfolio > p {
    padding: 0 20%;
  }
  .container-contacts {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
  }
  .main.contacts {
    padding: 100px 100px 40px;
  }
  .container-contact.dark {
    background: #1E293B;
  }
}
