.container.header {
  z-index: 999;
  position: sticky;
  top: 0;
  width: 100%;
  padding: 15px 20px;
  border-top: 1px solid transparent;
  border-bottom: 1px solid rgb(0, 0, 0, .1);
  background: rgb(255, 255, 255, .1);
  backdrop-filter: blur(5px);
}
.main.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.main.header > h3 {
  color: var(--color-default);
  font-size: 1.2em;
}
.icon-menu {
  font-size: 30px;
}
.main.header > ul {
  display: none;
}
.main.header > ul li {
  cursor: pointer;
}

/* dark mode */ 
.container.header.dark {
  background: rgb(15, 23, 24, .1);
}
.container.header.dark ul li {
  color: #fff;
} 

